import * as React from 'react';
import ReactDOM from 'react-dom';

import { Root, Epic, View, Panel, PanelHeader, Tabbar, TabbarItem, Button, Div, List, SimpleCell } from '@vkontakte/vkui';
import Icon28GlobeOutline from '@vkontakte/icons/dist/28/globe_outline';
import Icon28ListOutline from '@vkontakte/icons/dist/28/list_outline';
import '@vkontakte/vkui/dist/vkui.css';
import L from 'leaflet';

import master from './book.js';

//@TODO
//
//1. Cделать первые 10 глав книги (отдать ребятам, Яндекс.Толока)
//2. Кастомизировать popup
//3. Изменит стиль карты так, чтобы она подстраивалась под размер экрана

var book = undefined;
var book_chapters = [];
//Процедура загрузки книги
function load_book(str) {
  book = JSON.parse(str);

  for (var i = 1; i !== book.chapters; i++)
    book_chapters.push({key: i, value: [book.data[i].name, i, book.data[i].marks, book.data[i].data]});

};

load_book(master);
var popups = [];

async function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

var curr_point = L.latLng(55.7522, 37.6156); //Текущие координаты карты
var curr_z = 14; //Текущее увеличение карты

//Класс карты
export default class Map extends React.Component {
  constructor(props) {
    super(props);

    this.pause = false;
    this.persons = {};

    this.play = this.play.bind(this);
  }

  async play() {
    var first = true;
    while (popups.length > 0) {
      var datablock = popups[0];

      if (datablock[0] === "del") {
        if (datablock[1] in this.persons) {
          var buf = this.persons[datablock[1]];
          buf.remove();
          this.persons[datablock[1]] = undefined;
        }
      }
      else {
        if (datablock[0] in this.persons && this.persons[datablock[0]] !== undefined)
          this.persons[datablock[0]].remove();

        var point = L.latLng(datablock[1]);
        if (!(this.mymap.getBounds().contains(point)) || first)
          this.mymap.flyTo(point);
        this.persons[datablock[0]] = L.popup()
          .setLatLng(point)
          .setContent("<br>" + datablock[0] + "<br/>")
          .openOn(this.mymap);

        await sleep(datablock[2] * 2).then()
        first = false;
      }
      
      popups.shift();
      if (this.pause)
        break;
    };
  }

  //Рендер DOM
  render() {
    return (
      <div id="mapid" style={{height: document.documentElement.clientHeight - 120, width: document.documentElement.clientWidth}}></div>
    );
  }

  //Вторичный рендер данных карты
  componentDidMount() {
    //Создаём объект карты
    this.mymap = L.map('mapid').setView(curr_point, curr_z).setMaxBounds(L.latLngBounds(L.latLng(56.0, 37), L.latLng(55.5, 38.25)));
    //Отправляем запрос в Mapbox 
    L.tileLayer("https://api.mapbox.com/styles/v1/black-horse/ck9423ydx3ezj1ip93plhxxbb/tiles/{z}/{x}/{y}?access_token={accessToken}", {
      minZoom: 10,
      maxZoom: 17,
      updateWhenZooming: false,
      updateWhenIdle: true,
      bounds: L.latLngBounds(L.latLng(56.25, 35.0), L.latLng(55.25, 39.0)),
      accessToken: "pk.eyJ1IjoiYmxhY2staG9yc2UiLCJhIjoiY2thMndiMDg5MDdjMDNsbGt1eHdoYjR6diJ9.z1NmmuGOTudn-b5H5NDtWg"
    }).addTo(this.mymap);

    this.play();
  }

  //Удаление элемента с экрана
  componentWillUnmount() {
    //Запоминаем текущие параметры
    curr_point = this.mymap.getCenter();
    curr_z = this.mymap.getZoom();
    //Очищаем память
    this.mymap = null;
  }
}


class App extends React.Component {
  constructor (props) {
    super(props);

    this.state = {
      activeStory: 'map',
      activeScreen: 'intro',
      activePanelChapter: 'chapter_select',
      activeChapter: 1,
      MarksInChapter: []
    };
    this.onStoryChange = this.onStoryChange.bind(this);
    this.onIntroClick = this.onIntroClick.bind(this);
    this.onChapterCellClick = this.onChapterCellClick.bind(this);
    this.onMarkCellClick = this.onMarkCellClick.bind(this);
  }

  onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story });
  }

  onIntroClick (e) {
    this.setState({ activeScreen: 'things' });
  }

  onChapterCellClick(e, index) {
    var buf = [];
    var i = 0;
    book_chapters[index - 1].value[2].forEach(mark => {
      buf.push({key: i, value: mark});
      ++i;
    });
    
    this.setState({activeChapter: index, MarksInChapter: buf});
    this.setState({ activePanelChapter: 'mark_select' });
  }

  onMarkCellClick(e, str) {
    var mark_id = this.state.MarksInChapter[str].value[0];
    var found = false;

    book_chapters[this.state.activeChapter - 1].value[3].forEach(element => {
      if (found && element[0] !== "mark") 
        popups.push(element);
      if (element[0] === "mark" && element[1] === mark_id) {
        found = true;
      }
    });
    this.setState({ activePanelChapter: 'chapter_select', activeStory: 'map'});
  }
  render() {
    return (
      <Root activeView={this.state.activeScreen}>
        <View id="intro">         
          <Div style={{ paddingTop: 60, paddingBottom: 60, color: 'gray' }}>
            <p style={{textAlign: 'center', verticalAlign: 'middle'}}>Добро пожаловать!</p>
            <p>Это приложение разработано группой юных аналитиков в рамках проектной деятельности в Академии Калашников. Оно поможет людям, любящим книги и путешествия, узнать побольше о тех местах, 
            где происходили события из книги, и даже пройти по маршрутам её героев. Приложение понятно и легко в использовании.</p>
            <p>Продукт находится в активной разработке и совсем скоро сможет порадовать интересными новинками! Поэтому обязательно оставляйте обратную связь разработчикам или пишите свои пожелания.</p>
            <Button size="xl" mode="secondary" onClick={this.onIntroClick}>
              Продолжить
            </Button>
          </Div>
        </View>
        <Epic id="things" activeStory={this.state.activeStory} tabbar={
          <Tabbar>
            <TabbarItem
              onClick={this.onStoryChange}
              selected={this.state.activeStory === 'map'}
              data-story="map"
              text="Просмотр"
            ><Icon28GlobeOutline/></TabbarItem>
            <TabbarItem
              onClick={this.onStoryChange}
              selected={this.state.activeStory === 'chapter'}
              data-story="chapter"
              text="Главы"
            ><Icon28ListOutline/></TabbarItem>
          </Tabbar>
        }> 
          <View id="map" activePanel="map">
            <Panel id="map">
              <PanelHeader>Карта</PanelHeader>
              <Map/>
            </Panel>
          </View>
          <View id="chapter" activePanel={this.state.activePanelChapter}>
            <Panel id="chapter_select">
              <PanelHeader>Выбор главы</PanelHeader>
              <List>
                {Array.prototype.map.call(book_chapters, function (chapter_struct) {
                  return <SimpleCell expandable onClick={(e) => this.onChapterCellClick(e, chapter_struct.value[1])}>Глава {chapter_struct.value[1]}: {chapter_struct.value[0]}</SimpleCell>;
                }, this)}
              </List>
            </Panel>
            <Panel id="mark_select">
              <PanelHeader>Выбор закладки</PanelHeader>
              <List>
                {Array.prototype.map.call(this.state.MarksInChapter, function (mark_name) {
                return <SimpleCell expandble onClick={(e) => this.onMarkCellClick(e, mark_name.value[0])}>{mark_name.value[1]}</SimpleCell>;
                }, this)}
              </List>
            </Panel> 
          </View>
        </Epic>
      </Root>
    )
  }
}

ReactDOM.render(<App/>, document.getElementById('root'));